.Admin-profile-image-container {
    border-radius: 50%;
  }
  
  .Admin-profile-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 40px;
    border: 1px solid #ddd;
    background-color: white;
    padding: 2px;


  }
  @media only screen and (max-width: 1100px) {
    .Admin-profile-image  {
      width: 40px;
      height: 40px;
    }
  }
  